import React, { useState, useEffect } from 'react';
import { Table, Button, Popconfirm } from 'antd';
import { EditOutlined, CloseOutlined, QuestionCircleOutlined } from '@ant-design/icons';
import './style.css';
import Amount from 'arui-feather/amount';
import EditModal from '../EditModal';
import firebase from '../../config/fbConfig';

function ListPayments() {
  const [show, setShow] = useState(false);
  const [record, setRecord] = useState(null);
  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection('transactions').get();
      console.log(data.docs.map((doc) => ({ ...doc.data(), key: doc.id })));
      // console.log(data);
      // setSpells(data.docs.map(doc => ({ ...doc.data(), id: doc.id })));
    };
    fetchData();
  }, []);

  const columns = [
    {
      title: 'Дата',
      dataIndex: 'period',
      sorter: {
        compare: (a, b) => {
          const dateA = new Date(a.period);
          const dateB = new Date(b.period);
          return dateA - dateB;
        },
        multiple: 3,
      },
    },
    {
      title: 'Категория',
      dataIndex: 'category',
      sorter: {
        compare: (a, b) => {
          const nameA = a.category.toLowerCase();
          const nameB = b.category.toLowerCase();
          if (nameA < nameB) return -1;
          if (nameA > nameB) return 1;
          return 0;
        },
        multiple: 2,
      },
    },
    {
      title: 'Сумма',
      dataIndex: 'sum',
      sorter: {
        compare: (a, b) => a.sum - b.sum,
        multiple: 1,
      },
      render: (val) => (
        <div className="list__sum">
          <Amount size="s" amount={{ value: val, currency: { code: 'RUR', minority: 100 } }} className="" showZeroMinorPart />
        </div>
      ),
    },
    {
      title: 'Получатель',
      dataIndex: 'recipient',
      sorter: {
        compare: (a, b) => {
          const nameA = a.recipient.toLowerCase();
          const nameB = b.recipient.toLowerCase();
          if (nameA < nameB) return -1;
          if (nameA > nameB) return 1;
          return 0;
        },
        multiple: 4,
      },
    },
    {
      title: 'Действия',
      dataIndex: 'action',
      render: (text, curRecord) => (
        <div>
          <Button
            type="link"
            icon={<EditOutlined />}
            onClick={() => {
              setRecord(curRecord);
              setShow(true);
            }}
          />
          <Popconfirm title="Удалить запись?" onConfirm={() => console.log(record.key)} cancelText="Отмена" icon={<QuestionCircleOutlined style={{ color: 'red' }} />}>
            <Button type="link" icon={<CloseOutlined />} />
          </Popconfirm>
        </div>
      ),
    },
  ];

  const data = [
    {
      key: '1',
      period: '06.03.2020',
      category: 'Транспорт',
      sum: -2600,
      recipient: '',
    },
    {
      key: '2',
      period: '06.03.2020',
      category: 'Продукты, еда',
      sum: -26000,
      recipient: 'Магнит',
    },
    {
      key: '3',
      period: '05.03.2020',
      category: 'Образование',
      sum: -8700,
      recipient: 'Веб-сайт',
    },
    {
      key: '4',
      period: '05.03.2020',
      category: 'Здоровье',
      sum: -4990,
      recipient: 'Аптека',
    },
    {
      key: '5',
      period: '04.03.2020',
      category: 'Зарплата',
      sum: 1000000,
      recipient: 'Работа',
    },
  ];

  const param = {
    show,
    onShow: () => setShow(false),
    record,
    key: record == null ? 0 : record.key,
  };

  return (
    <>
      <EditModal {...param} />
      <Table columns={columns} dataSource={data} pagination={false} />
    </>
  );
}

export default ListPayments;
